import React from 'react'
import Link from 'gatsby-link'

import styles from './header.module.css'

export default () => 
    <header className={styles.navigation}>
        <Link to="/">
            <img src={__PATH_PREFIX__ + `/tmwed.svg`} alt="tmwed" />
        </Link>
    </header>


